@font-face {
  font-family: 'Montserrat';
  src: url('../../assets/fonts/static/Montserrat-Medium.ttf');
}

.block {
  text-align: center;
}
.btnPag {
  z-index: 103;
  cursor: pointer;
  border: none;
  margin-top: 26px;

  background-color: #bf9ee2;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.55);
  border-radius: 10.3108px;
  padding: 10px 20px;
  margin-bottom: 30px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #373737;
}
.btnPag:hover {
  background-color: #5cd3a8;
}
.btnPagArrow {
  z-index: 103;
  cursor: pointer;
  border: none;
  margin-top: 26px;

  background-color: #bf9ee2;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.55);
  border-radius: 50%;
  padding: 10px 14px;
  margin-bottom: 30px;
}

.btnPagArrow:hover {
  background-color: #5cd3a8;
}
.loader {
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 50%;
}
